import React, { useState } from "react";
import CryptoJS from 'crypto-js';
import { useEffect } from "react";

// eslint-disable-next-line no-extend-native
String.prototype.capitalize = function () {
  return this.charAt(0).toUpperCase() + this.slice(1);
}
function DecryptFormField(props) {
  const [key, setKey] = useState("");
  const [error, setError] = useState("");
  const [ful, setFul] = useState(false);


  useEffect(() => {
    if (!ful && props.secret) {
      setKey(props.secret);
      setFul(true)
      decrypt(props.secret);
      async function sleep(ms) { await new Promise(r => setTimeout(r, ms)) }
      sleep(500).then(() => {
      })
    }
  })

  const handleSubmit = (evt) => {
    evt.preventDefault();
    decrypt(key)
  }

  const onKeyChanged = (e) => {
    setKey(e.target.value);
    setError('')
  }

  const decrypt = (key) => {
    const kw = CryptoJS.enc.Base64.parse(key);
    const iv = CryptoJS.enc.Base64.parse('PPcpLTnbHuAvUQCAjqQrAg');

    // Decrypt
    const bytes = CryptoJS.AES.decrypt(props.ciphertext, kw, { iv });
    const originalText = bytes.toString(CryptoJS.enc.Utf8);

    if (!originalText || originalText === '') {
      setError('Fel nyckel :(')
    } else {
      props.onSuccess(originalText)
    }
  }
  return (
    <form onSubmit={handleSubmit}>
      <label className='password'>
        <input type='password' placeholder='Nyckel' onChange={onKeyChanged} value={key} />
      </label>
      <div className="error">{error}</div>
    </form>
  );
}

export default function Test({ pageContext: { data } }) {
  const [state, setState] = useState('talk')
  const [gifter, setGifter] = useState({})
  const [receiver, setReceiver] = useState('')
  const [secret, setSecret] = useState('')


  useEffect(() => {
    const params = new URLSearchParams(window.location.search);
    const gifterName = params.get("g");
    const secret = params.get("secret");

    const g = data.find((s) => s.name === gifterName)
    if (g) {
      setGifter(g)
      setState('password')
    }

    if (secret) {
      setSecret(secret)
    }

  }, [data])

  const onSuccess = (name) => {
    setReceiver(name)
    setState('person')
  }

  return (
    <div id='app' data-state={state}>
      <div action='' className='fake-form'>
        <fieldset className='big-radio' data-group="talk" onClick={() => setState('password')}>
          <legend>Vem r du?</legend>
          <p>Ta reda p vem du ska vara tomte fr i r, 2022</p>
          <div className='radio-group'>
            {
              data.map((d, i) => {
                return (<label key={d.name} className='radio' style={{ '--i': i }}>
                  <input type='radio' name='person' onChange={(b) => b ? setGifter(data[i]) : null } />
                  <span>{d.name.capitalize()}</span>
                </label>
                )
              })
            }
          </div>
        </fieldset>
        <fieldset className='password' data-group="password">
          <legend>
            <button className='back' type='button' onClick={() => setState('talk')}></button>Ange din hemliga nyckel
          </legend>
          <DecryptFormField secret={secret} ciphertext={gifter?.ciphertext} onSuccess={onSuccess}/>
        </fieldset>
        <fieldset className='person' data-group="person">
          <legend>
            <button className='back' type='button' onClick={() => setState('talk')}></button>
            {gifter.name?.capitalize()} fr agera tomte till</legend>
          <img src='static/horn.gif' alt='Santa' align='right'/>
          <p>{receiver.capitalize()}</p>
        </fieldset>
      </div>
    </div>
  )
}